
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
/** iPad **/
@media only screen and (min-width: 768px) {
.nav-bar {
    width: 100%;
    height: 50px;
    background-color: #191818;
    position: fixed;
    top: 0;
}
.nav-bar .container {
    width: 80%;
    height: 100%;
    margin: 0 auto;
}
.nav-bar .container .container-left {
    width: 30%;
    height: 100%;
    float: left;
    /*border: 1px solid #000;*/
}
.nav-bar .container .container-left h3 {
    color: #eee;
    font-size: 15px;
    line-height: 50px;
}
.nav-bar .container .container-right {
    width: 70%;
    /*border: 1px solid #000;*/
    float: left;
}
.nav-bar .container .container-right ul {
    text-align: center;
}
.nav-bar .container .container-right ul li {
    float: left;
    padding: 0 5px;
    margin: 0 5px;
}
.nav-bar .container .container-right ul li span {
    color: #eee;
    font-size: 13px;
    line-height: 50px;
}
.nav-bar .container .container-right ul li:hover {
    background-color: #3F4042;
}
}
/** iPhone **/
/*640/20=32 85.3333/32*/
@media only screen and (min-width: 320px) and (max-width: 750px) {
.nav-bar {
    width: 100%;
    height: 2.66666562rem;
    background-color: #191818;
    position: fixed;
    top: 0;
}
.nav-bar .container {
    width: 100%;
    height: 2.66666562rem;
    display: -webkit-flex;
}
.nav-bar .container .container-left {
    -webkit-flex: 8.7;
    display: -webkit-flex;
    text-align: center;
    -webkit-align-items: center;
}
.nav-bar .container .container-left h3 {
    color: #eee;
    font-size: 0.5625rem;
    /*padding: 0 5px;*/
    -webkit-flex: 1;
}
.nav-bar .container .container-right {
    width: 100%;
    z-index: 999;
    position: fixed;
    top: 2.66666562rem;
    left: 0;
    -webkit-animation: navAnimation 1s linear;
}
.nav-bar .container .container-right ul {
    text-align: center;
}
.nav-bar .container .container-right ul li {
    padding: 0.3125rem 0;
    margin-top: 0.09375rem;
    -webkit-box-shadow: -0.15625rem 0.03125rem 0.625rem #000;
    background-color: #191818 ;
}
.nav-bar .container .container-right ul li span {
    font-size: 0.5rem;
    color: #eee;
}
.nav-bar .container .switch {
    -webkit-flex: 1.3;
    display: -webkit-flex;
    -webkit-align-items: center;
    border: 1px solid #eee;
}
.nav-bar .container .switch ul {
    width: 100%;
}
.nav-bar .container .switch ul li {
    width: 60%;
    border-bottom: 2px solid #eee;
    margin: 5px auto;
}
.activeBg {
    background-color: #3F4042 !important;
}
@-webkit-keyframes navAnimation {
    /* Safari å’Œ Chrome */
0% {
      height: 0;
      width: 0;
}
100% {
      height: 100%;
      width: 100%;
}
}
}



















